import React from 'react';
import {BrowserRouter,Route } from 'react-router-dom';
import List from './List/ListingApi';
import RestDetail from './Details/RestaurentDetails';
import Home from './Home/Home';
import Header from './Header';
import Footer from './Footer';
import Bookings from './orders/bookingApi';
import Placeorder from './orders/bookingForm';

const Routing = () =>{
    return(
        <BrowserRouter>
             <div>
                 <Header/>
                 <Route exact path='/'  component={Home}></Route>
                 <Route path='/list/:id'  component={List}></Route>
                 <Route path='/details/:id'  component={RestDetail}></Route>
                 <Route path='/viewBooking'  component={Bookings}></Route>
                 <Route path='/booking/:id'  component={Placeorder}></Route>
                 <Footer/>
             </div>
        </BrowserRouter>
    )
}

export default Routing;
























